body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans',-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    color: #828286;
    font-weight: 600;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {     
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  cursor:pointer;
  overflow: hidden;        
}

.fa {
  color: #828286;
}

.fa:hover {
  color: #555557;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbarStyle ul {
  list-style: none; /* Remove list bullets */
    padding-top: 30px;
    padding-bottom: 0px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.navbarStyle ul li {
  margin-left: 40px;
  margin-right: 40px;
}

.navbarStyle hr {
  margin-bottom: 0px;
  margin-top: 0px;
}

.navbarStyle ul li a{
  color: #828286;
}
.notes {
width: 70%;
display: flex;
flex-direction: column;
margin: 0 auto;

}

.notesAndCreate {
display: flex;
flex-direction: column;
margin: 0 auto;
}

.allNotes {
  display: flex;
flex-direction: row;
flex-wrap: wrap;
}

.allNotes .noteColor {
  border: 1px solid #dadce0;
  border-radius: 10px;
  font-family: 'Open Sans',Roboto,Arial,sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 1.5rem;
}

.noteColor {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 20px;
  padding: 20px;
  max-height: 300px;
  max-width: 300px;
}

.noteColor a {
  color: #202124;
  font-size: 1.2em;
}

.noteColor p {
  color: #202124;
  font-size: 0.8em;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.noteColor a:hover {
  color: rgb(129, 0, 22);
  text-decoration: none;
}

.noteLightBlue {
  background-color: #add8e6;
}

.noteCoral {
  background-color: #ff9966;
}

.noteAqua {
  background-color: #b8ffd6;
}

.noteFuxia {
  background-color: #f2a7ff;
}


.noteCoral {
  background-color: #ff9966;
}

.noteLightgreen {
  background-color: lightgreen;
}

.noteFabada {
  background-color: #fabada;
}

.noteYellow {
  background-color: #f5ef9f;
}



.createNote {
  margin: 40px;
}

a {
  color: #828286;
}

.showNoteTools {
  display: flex;
  flex-direction: row;
  justify-content: start;
}

.showNoteTools button {
  padding-right: 20px;
}

.allNotes .noteColor:hover {
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.2), 0 1px 5px 0 rgba(0,0,0,0.12);
}

.ql-toolbar span {
  background-color: transparent !important;
}

.ql-toolbar.ql-snow {
  border: none !important;
}

.ql-container.ql-snow {
  border-left: none !important;
  border-right: none !important;
  width: 80%;
  margin: 0 auto;
}

.createNoteBox {
  margin: 5% auto;
  width: 70%;
  border-radius: 10px;
  background-color: white;
  padding: 15px;
}

input {
  border-width: 0px;
  outline: none;
  margin: 3px;
  padding: 10px;
  border-bottom: 1px solid lightgrey;
}

.quill {
  margin: 30px;
}

.createNoteBox h2 {
  margin: 30px;
}

.createNoteBox:hover{
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.2), 0 1px 5px 0 rgba(0,0,0,0.12);
}

input[type=file] {
  cursor: pointer;
  width: 180px;
  height: 34px;
  overflow: hidden;
  border-bottom: none;
}

input[type=submit] {
  cursor: pointer;
  background: #39D2B4;
  border-radius: 5px;
}

input[type=submit]:hover {
  background: rgb(15, 43, 87);
  color: rgb(255, 212, 188);
}

input[type=file]:before {
  width: 158px;
  height: 40px;
  font-size: 16px;
  line-height: 32px;
  content: 'Select your file';
  display: inline-block;
  background: white;
  padding: 0 15px;
  text-align: center;
}

input[type=file]::-webkit-file-upload-button {
  visibility: hidden;
}

.noteTools {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.createNoteTitle {
  width: 70%;
}

.leftButton {
  float: left;
}

.popup-overlay {
  overflow-y: scroll;
}

code {
  text-align: left !important;
}



.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.userNamePhoto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
}

.userNamePhoto img{
  width: 200px;
  height: 200px;
  border-radius: 50%
}
.popup-content {
  width: auto !important;
  padding: 20px !important;
  border-radius: 10px;
  min-width: 30% !important;
  max-width: 65% !important;
}

:root {
  --yellow: #ffc600;
  --black: #272727;
}


body {
  background-image: -webkit-linear-gradient(340deg, #fffaf9, #eae9ec);
  background-image: linear-gradient(110deg, #fffaf9, #eae9ec);
  background-position: fixed;
}

#mySketch > span {
  background-color: darkgrey;
  padding: 0.3rem;
  font-size: 0.8em;
  font-family: monospace;
}

p {
  line-height: 1.6em;
}

.tools {
  display: flex;
  justify-content: space-between;
  width: 400px;
}

input[type=number] {
  width: 50px;
}
.home-boxes {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.home-boxes div {
  width: 20%;
  margin: 20px;
}

.homeBody h1 {
  font-weight: 800;
  margin-top: 60px;
  margin-bottom: 40px;
}

.logoEnot {
  width: 200px;
  height: 160px;
}

.logoLine {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  justify-content: center;
  margin-top: 60px;
  margin-bottom: 60px;
}
